.menuItem {
  min-width: 30%;
  height: 240px;

  display: flex;
  align-items: center;
  justify-content: center;
  flex: 1 1 auto;

  margin: 0 7.5px 15px;

  overflow: hidden;
  border: 1px solid black;

  &:hover {
    cursor: pointer;

    & .backgroundImage {
      transform: scale(1.1);
      transition: transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);
    }

    & .content {
      opacity: 0.9;
    }
  }

  &.large {
    height: 380px;
  }

  &:first-child {
    margin-right: 7.5px;
  }

  &:last-child {
    margin-left: 7.5px;
  }

  .backgroundImage {
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
  }

  .content {
    position: absolute;
    height: 90px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    padding: 0 25px;

    border: 1px solid black;
    background-color: white;
    opacity: 0.7;


    .title {
      font-weight: bold;
      margin: 0 6px 0;
      font-size: 22px;
      color: #4a4a4a;
    }

    .subtitle {
      font-weight: lighter;
      font-size: 16px;
    }
  }
}